body {
  font-family: $main-font;
  font-size: 14px;
  letter-spacing: 0.03em;
  color: $color-dark;
  background-color: $color-white;
  margin: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

ul,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  list-style: none;
  margin: 0px;
  padding: 0px;
}

img {
  display: block;
  height: auto;
  object-fit: cover;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  width: 1230px;
  // @media screen and (min-width: 768px) {
  //     width: 768px;
  // }

  // @media screen and (min-width: 1200px) {
  //     width: 1200px;
  // }
}

.link,
.link:hover,
.link:focus {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

